
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
data () {
return {
form: {}
}
},
methods: {
to2020 () {
this.$router.push({
path: '/estimate2020'
})
},
_getDetail () {
this.$tupHttp(this, 'manage-psi/profitCalculation/permission-all/getProfitCalculationToApp', 'POST').then(res => {
this.form = res.data.model
})
}
},
created () {
this._getDetail()
},
components: {
XHeader
}
}



